import { useInit } from "../utils";
import { useApp } from "./AppContext";

export default function FeedbackAnalysis({ userRating, action, actionQueueState, examineState }) {
    const app = useApp();
    const [, setActionQueue] = actionQueueState;
    const [examineQueue, isExamining] = examineState;

    const good     = filteredByStatus("good");
    const absent   = filteredByStatus("absent");
    const errors   = filteredByStatus("errors");
    const nonRequired  = action.subActions.filter(e => e.type?.toLowerCase() !== "required" && e.log_status && e.log_status !== "notFound");
    const outOfOrder   = action.subActions.filter(e => e.type === "required" && e.log_phaseOrderStatus !== "good");
    const systemRating = action.log_status;
    const willExamine  = examineQueue.length > 0;

    // console.log(action);

    useInit(() => {
        if (action.id[0] === "top" && action.log_status === "absent") {
            app.setNextDisabled(true);

            setTimeout(() => {
                window.location = "https://www.meddebriefer.org/mockup/#/";
            }, 10_000); // Is a 10 second redirect too long?
        }

        if (action.log_status === "absent") {
            // A symptom of id's being arrays.....
            setActionQueue(queue => queue.filter(act => !action.subActions.some(e => e.id.some(id => act.id.includes(id)))))
        }
    });

    // Edge case where the top level is absent (aka nothing was done)
    if (action.id[0] === "top" 
            && action.log_status === "absent") {
        return (
            <div className="analysis feedback-block">
                <div className="rating">
                    <p><b>System Rating:  </b>{ systemRating }</p>
                    <p><b>Student Rating: </b>{ userRating }</p>
                </div>

                <p>Nothing was completed. You'll now be redirected to restart the scenario...</p>
            </div>
        );
    }

    // Edge case if parent is absent, just return absent feedback
    if (action.log_status === "absent") {
        return (
            <div className="analysis feedback-block">
                <div className="rating">
                    <p><b>System Rating:  </b>{ systemRating }</p>
                    <p><b>Student Rating: </b>{ userRating }</p>
                </div>

                <p>{action.feedbackAbsent}</p>      
            </div>
        );
    }

    // Main case
    return (
        <div className="analysis feedback-block">
            <div className="rating">
                <p><b>System Rating:  </b>{ systemRating }</p>
                <p><b>Student Rating: </b>{ userRating }</p>
            </div>

            <div className="feedback-list">
                <p>{getFeedbackResponse()}</p>

                <LabeledUL
                    label="Goals that you did complete:"
                    list={good}
                    targetProp={null}
                    examineState={[examineQueue, isExamining]}
                />

                <LabeledUL
                    label="Goals that you did not successfully complete:"
                    list={errors}
                    targetProp="feedbackErrors"
                    examineState={[examineQueue, isExamining]}
                />

                <LabeledUL
                    label={(good.length > 0 || errors.length > 0)
                        ? "In addition, you did not address these goals:"
                        : "You did not address these goals:"}
                    list={absent}
                    targetProp="feedbackAbsent"
                    examineState={[examineQueue, isExamining]}
                />

                <LabeledUL 
                    label={"You performed the following actions out of order:"}
                    list={outOfOrder}
                    targetProp="feedbackOutOfOrder"
                    examineState={[examineQueue, isExamining]}
                />

                <LabeledUL 
                    label={"In addition you did actions that were not indicated in this scenario:"}
                    list={nonRequired}
                    targetProp="feedbackErrors"
                    examineState={[examineQueue, isExamining]}
                />

                {/* If we're going to examine: */}
                {willExamine && 
                    <p>Let's take a closer look...</p>}

                {/* If there was data on the parent, but nothing for the children */}
                {/* This shouldn't really happen unless the data is broken */}
                {absent.length === 0 && errors.length === 0 && good.length === 0 &&
                    <p><i>No data here yet.</i></p>}
            </div>
        </div>
    );

    function filteredByStatus(status) {
        return action.subActions.filter(a => a.log_status === status);
    }

    function getFeedbackResponse() {
        if (userRating === "Unsure")
            return "Let's review...";

        if ((userRating === "No" && (systemRating === "errors" || systemRating === "absent"))
            || (userRating === "Yes" && systemRating === "good"))
            return "I agree!";

        return "I disagree...";
    }
}

// **************************************************************** \\

function LabeledUL({ label, list, targetProp, examineState }) {
    const [examineQueue, isExamining] = examineState;

    if (list.length === 0) return null;

    return (
        <>
        <p><b>{ label }</b></p>
        <ul>
            {list.map((item, idx) => (
                <li key={`${item.id}-${idx}`} className={examineQueue[0]?.id === item.id && isExamining ? "active-examine" : ""}>
                    <p>{ item.label }</p>

                    {item[targetProp] && 
                        <p className="tabbed"><i>{ item[targetProp] }</i></p>}
                </li>
            ))}
        </ul>
        </>
    )
}