import { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { Table } from "reactstrap";

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getLogs } from "@meddbriefer/scenario-data/index"


export default function LogList() {
    const {db} = useFirebase()
    const [logs, setLogs] = useState([])
    const [logsLoaded, setLogsLoaded] = useState(false)

    useEffect(
        () => {
            getLogs(db)
            .then(result => {
                setLogs(result)
                // console.log(result)
                setLogsLoaded(true)
            })
            .catch(error => console.log('error fetching logs', error))
        },
        [db]
    )

    if (!logsLoaded) {
        return (<h4>fetching log data</h4>)
    }
    return (
        <Table striped={true} borderless size="sm" hover>
            <thead>
                <tr>
                    <th>Label</th>
                    <th>Scenario</th>
                    <th>Username</th>
                    <th>Timestamp</th>
                </tr>
            </thead>
            <tbody>
                {logs.map(log => (
                    <tr key={log.id}>
                        <td>{log.label}</td>
                        <td>{log.scenario}</td>
                        <td>{log.userName}</td>
                        <td>{log.timestamp}</td>
                        <td>
                            <Link to={`/logs/${log.id}`}>Analyze</Link>&nbsp;&nbsp;&nbsp;&nbsp;
                            <Link to={`/debriefing-feedback-logs/${log.id}`}>Display</Link>
                        </td>
                    </tr>
                ))}
            </tbody>
        </Table>
    )

}
