import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getLog , getPublishedScenarioObjectByID } from "@meddbriefer/scenario-data/index"

import AnalyzeDebriefing from './AnalyzeDebriefing'

export default function Log() {
  const {db} = useFirebase()
  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(
    () => {
      getLog(db,logID)
        .then(logData => {
          setLog(logData)
        })
    }, [db, logID]
  )

  useEffect(
    () => {
      !!log && getPublishedScenarioObjectByID(db, log.scenarioID)
        .then(scenData => {
          setScenario(scenData)
          console.log("scenario", scenData)
        })

    }, [db, log]
  )

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    }, [scenario]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return (
      <AnalyzeDebriefing
        logID={logID}
        observerLog={log}
        scenario={scenario}

      />
    )
}