import { useApp } from './AppContext';

const TopButton = ({ phase }) => {
    const app = useApp(); 

    const jumpPhase = () => {
        app.setCurrPhase(phase);

        // if (app.isEnabled(phase)) {
        //     app.setCurrPhase(phase);
        // }
    };

    return (
        <button 
            className={`phaseTabs ${app.currPhase === phase ? "selected-btn" : ""}`}
            type="button"
            // disabled={!app.isEnabled(phase)}
            onClick={jumpPhase}
        >
            {phase}
        </button>
    );
} 

export default TopButton; 