export const saveAnalysisLog = (db, log, actions, constraintsViolated) => {
  saveLog(
    db,
    "debriefing-feedback-logs",
    log,
    actions,
    constraintsViolated,
    log.label,
    ""
  );
};

export const saveCommentLog = (db, log, actions, userName, constraintsViolated) => {
  window.alert(`${log.label} saved`);
  saveLog(
    db,
    "debriefing-comment-logs",
    log,
    actions,
    constraintsViolated,
    log.label,
    userName
  );
};

export const saveAsCommentLog = (
    db,
    log,
    actions,
    userName,
    constraintsViolated
) => {
  let filename = window.prompt("Enter new filename", log.label);
  if (filename) {
    log = saveLog(
        db,
        "debriefing-comment-logs",
        log,
        actions,
        constraintsViolated,
        filename,
        userName
    );
  }
  return log;
};

export const saveLog = (
  db,
  collection,
  log,
  actions,
  constraintsViolated,
  filename,
  userName
) => {
  //console.log("In saveLog")
  let fileID = log.id;
  if (filename !== log.label) {
    fileID = Date.now() + "-" + userName;
  }
  const docRef = db.collection(collection).doc(fileID);
  let data = {
    userName: log.userName,
    ObserverFileCreater: log.userName,
    label: filename,
    ObserverFileName: log.label,
    commenter: userName,
    scenarioID: log.scenarioID,
    scenarioName: log.scenarioName,
    schemaVersion: log.schemaVersion,
    timestamp: Date().toLocaleString(),
  };

  if (!!constraintsViolated) {
    data.constraintsViolated = constraintsViolated;
  }
  if (!!c2FB) {
    data.FBfields = c2FB;
  }

  let newActionsList = [];
  let newObj = {};
  let filterUnknown = false;

  //included for debugging puposes only
  actions.forEach(function (logObj, index) {
    for (let [key, val] of Object.entries(logObj)) {
      if (undefined === val) {
        console.log(
          "filtered out from events; index: ",
          index,
          " key: ",
          key,
          " value: ",
          val
        );
        filterUnknown = true;
      }
    }
  });

  if (filterUnknown) {
    actions.forEach(function (logObj, index) {
      newObj = _.pickBy(logObj, function (value, key) {
        return !(value === undefined);
      });
      newActionsList.push(newObj);
    });
    data["events"] = newActionsList;
  } else data["events"] = actions;

  //data["events"] = formattedActions;
  //console.log(JSON.stringify(data, null, 4))
  docRef.set(data);
  log.label = filename;
  log.id = fileID;
  return log;
};

