import React, { useState, useEffect } from 'react';
import { useApp } from './AppContext';
import Tooltip from './Tooltip';

const SlideShow = ({ phase, children }) => {
    const app = useApp();
    const [curSlide, setCurSlide] = useState(0);
    const slideshowData = app.stepData.find(step => step.step === app.currPhase);

    useEffect(() => {
        app.enablePhase(phase)
        app.setCurrPhase(phase);

        app.setNextEvent(() => () => {
            if (curSlide >= slideshowData.slides.length - 1) {
                app.setCurrPhase(app.next_links[phase] ?? phase)
            } else {
                setCurSlide(slide => slide + 1);
            }
        });

        app.setBackEvent(() => () => {
            if (curSlide === 0) {
                app.setCurrPhase(app.prev_links[phase] ?? phase)
            } else {
                setCurSlide(slide => slide - 1);
            }
        });
    // eslint-disable-next-line
    }, [phase, curSlide]);

    return (
    <>
        <h2><b>R</b>eview what was supposed to happen:</h2>
        <div className="slideshow card">
            <div className="card-body">
                <p className="hint-text">{curSlide + 1} of {slideshowData.slides.length}</p>
                {typeof slideshowData?.slides[curSlide]?.text === "object"
                    ? <Tooltip html={slideshowData.slides[curSlide].text} />
                    : <p>{slideshowData.slides[curSlide].text}</p>}
            </div>
        </div>
    </>
    );
}

export default SlideShow;
