import React, {useEffect} from "react";
import { useApp } from "./AppContext";

const CardText = ({ phase, children, list, form, text }) => {
    const app = useApp();
    const phaseData = app.stepData.find(e => e.step === phase);

    console.log(!!text, !!list)
    // console.log(phaseData)

    useEffect(() => {
        app.enablePhase(phase);
        app.setCurrPhase(phase);

        app.setNextEvent(() => () =>
            app.setCurrPhase(app.next_links[phase] ?? phase));

        app.setBackEvent(() => () =>
            app.setCurrPhase(app.prev_links[phase] ?? phase));
    // eslint-disable-next-line
    }, [phase, phaseData]);

    return (
        <>
            <h2 className="phase-heading">
                <b>{phaseData.title.charAt(0)}</b>
                {phaseData.title.substring(1, phaseData.length)}
            </h2>

            <div className="card">
                <div className="card-body">
                    {phaseData.description && 
                        <i>{phaseData.description}</i>}

                    {phaseData.heading && 
                        <i>{phaseData.heading}</i>}

                    {phaseData.questions &&
                        <p>{phaseData.questions}</p>}

                    {!!text && (Array.isArray(phaseData.text) ? phaseData.text.map((t, i) => (
                            <p key={i}>{t}</p>
                        )) : <p>{phaseData.text}</p>)}

                    {!!list && 
                        <ul>
                        {Array.isArray(phaseData.text) ? phaseData.text.map((d, i) => (
                            <li key={i}><p>{d}</p></li>
                        )) : phaseData.text}
                        </ul>}

                    {form && 
                        <textarea />}

                    {children}
                </div>
            </div>
        </>
    );
}

export default CardText;