import { useEffect, useRef } from "react";

export default function ExamineForm({ examine }) {
    const textRef = useRef();

    useEffect(() => {
        textRef.current.value = "";
    }, [examine]);

    if (!examine) return null;

    return (
        <div className="feedback-form feedback-block">
            <p><b>Let's reflect on: <i>{examine.label}</i></b></p>

            {examine.type?.toLowerCase() === "required" 
                ? <p>What did you do instead? What do you think led to this error?</p>
                : <p>Why did you perform this action?</p>}

            {!!examine.prompts 
                && 
                <>
                <p><b>For example:</b></p> 
                <ul>
                {examine.prompts.split(";").map((prompt, i) => (
                    <li key={i}>{ prompt }</li>
                ))}
                </ul>
                </>}

            <textarea 
                ref={textRef}
                placeholder="Enter your response here..."
            />
        </div>
    );
};