import {
    HashRouter as Router,
    Switch
} from "react-router-dom";

import {useFirebaseProject, FirebaseContext} from "@meddbriefer/mdb-firebase"
import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";

// import "./styles.css";
// import "./styles.scss"; // In process of migrating to Sass

import { environment } from "./environments/environment";

import UnauthedLayout from './components/UnauthedLayout';

import LogList from "./components/LogList"
import ObLog from "./components/ObLog"
import DebLog from "./components/DebLog"

export default function App() {
    const {auth, db} = useFirebaseProject(environment.firebaseProject)
    return (
      <FirebaseContext.Provider
        value={{
            auth,
            db
        }}
      >
        <AuthProvider>
          <Router>
            <Switch>
              <PrivateRoute
                exact
                path="/logs/:logID"
                authFlowLayout={UnauthedLayout}
                component={ObLog}
              />
              <PrivateRoute
                exact
                path="/debriefing-feedback-logs/:logID"
                authFlowLayout={UnauthedLayout}
                component={DebLog}
              />
              <PrivateRoute
                exact
                path="/"
                authFlowLayout={UnauthedLayout}
                component={LogList}
              />
            </Switch>
          </Router>
        </AuthProvider>
      </FirebaseContext.Provider>
    )
}