export default function FeedbackPrompt({ action, ratingState }) {
    const [userRating, setUserRating] = ratingState;
    const handleRating = (e) => setUserRating(e.target.value);  

    return (
        <div className="feedback-prompt feedback-block">
            {
                action.subActions.length > 0 && 
                <>
                    <p><b>Did you <i>{ action.label }</i>, including:</b></p>

                    <ol>
                        {action.subActions.filter(a => a.type?.toLowerCase() === "required").map((subaction, idx) => (
                            <li key={`${subaction.id}-${idx}`}>
                                <p>{ subaction.label }</p>
                            </li>
                        ))}
                    </ol>
                </>
            }

            <div className="rating-btns">
                <RatingButton rating="Yes"    />
                <RatingButton rating="No"     />
                <RatingButton rating="Unsure" />
            </div>
        </div>
    );

    function RatingButton({ rating }) {
        return (
            <button 
                onClick={handleRating} 
                disabled={!!userRating}
                type="button" 
                value={rating}
                className={userRating === rating ? "selected-rating" : ""}
            >
                { rating }
            </button>
        );
    }
}

