const findHeaderFor = (numericalID, log) => {
    let candidateHeader = {};
    let foundHeader = {};
    for (let entry of log) {
        if (entry.id === "problemHeader") {
            candidateHeader = entry;
        }
        else {
            if (entry.numericalID === numericalID) {
                foundHeader = candidateHeader;
                break;
            }
        }
    }
    return (foundHeader);
};

