import { useState, useEffect } from 'react';

import { useFirebase } from "@meddbriefer/mdb-firebase"
import {analyzeEvents} from '@meddbriefer/analysis'
import { verifyDocument } from '@meddbriefer/scenario-data/index';

import DebLog from './DebLog';


export default function AnalyzeDebriefing ({logID, observerLog, scenario})  {
  const {db} = useFirebase()
  const [analyzed, setAnalyzed] = useState(false)

    // eslint-disable-next-line

    // Set to true once all performed actions have been displayed in the log
    // const [finishedPerformedActions, setFinishedPerformedActions] = useState(false);

    //console.log ("logID:", logID)

    function checkOnOverwriting() {
        if (window.confirm ("Are you sure you want to overwrite the existing analysis?  Any prior comments previously added will be lost.")){
            analyzeEventsHook()

        }
    }

    async function analyzeEventsHook() {
      analyzeEvents(db, scenario, observerLog)
    }


    async function processEvents ()  {
        console.log("in processEvents to process a new observer file")
        if (! await verifyDocument(db, "debriefing-feedback-logs", logID))
            {analyzeEventsHook()
            setAnalyzed(true)}
        else {checkOnOverwriting()}
        setAnalyzed(true)
        return (true)
    }

    useEffect(
      () => {
        processEvents()
        console.log("check on analysis")
      // eslint-disable-next-line
      }, []
    )

    if (!analyzed) {
      return (<h3>Analyzing</h3>)
    }

    return (
        <DebLog
          logID={logID}
        />
    )
}
