import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getDebriefLog , getPublishedScenarioObjectByID } from "@meddbriefer/scenario-data/index"

import MainOutline from './MainOutline'

export default function DebLog() {
  const {db} = useFirebase()
  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(
    () => {
      getDebriefLog(db, logID)
        .then(logData => {
          console.log("retrieve Debriefing result in DebLog:", logData)
          setLog(logData)
        })
        .catch(error => alert('error fetching debriefing log', error))
    // eslint-disable-next-line
    }, [logID]
  )

  useEffect(
    () => {
      //!!log && getScenarioByName(db, log.scenarioName, "V2")
      !!log && getPublishedScenarioObjectByID(db, log.scenarioID)
        .then(scenData => {
          setScenario(scenData)
          console.log("scenario in DebLog", scenData)
        })

    }, [db, log]
  )

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    }, [scenario]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return (
      <MainOutline
        log={log}
        scenario={scenario}
      />
    )

}
