import React, { useState } from "react";
import Controller from "./Controller";
import { AppContext } from "./AppContext";

import Header from "./Header";

import TopButton from "./TopButton.js";
import CardText from "./CardText.js"
import SlideShow from "./SlideShow.js"
import TwoDivOutline from "./TwoDivOutline.js"
import FeedbackSlides from "./FeedbackSlides";

// import { STEP_DATA } from "../data/m2ca.js";
import { STEP_DATA } from "@meddbriefer/feedback-data/SC8CP_DEBR_Data";
import { PHASES, INITIAL_STATE, NEXT_LINKS, PREV_LINKS } from "../constants";

const MainOutline = ({log, scenario}) => {
    const [currPhase, setCurrPhase]       = useState(PHASES.D);
    const [phaseEnabled, setPhaseEnabled] = useState(INITIAL_STATE);
	const [nextEvent, setNextEvent] 	  = useState();
	const [backEvent, setBackEvent] 	  = useState();
	const [nextDisabled, setNextDisabled] = useState(false);

	const isCurrPhase 	= (phaseID) => currPhase === phaseID;
	const isEnabled 	= (phaseID) => phaseEnabled[phaseID];
    const enablePhase 	= (phaseID) => 
		setPhaseEnabled(prevState => ({ ...prevState, [phaseID]: true }));

    return (
		<div className="main-outline">
			<Header />
            
			<AppContext.Provider
				value={{
					stepData: 		STEP_DATA, 
					next_links: 	NEXT_LINKS, 
					prev_links: 	PREV_LINKS,
					enablePhase,
					currPhase, 
					setCurrPhase, 
					isCurrPhase, 
					isEnabled,
					nextEvent,
					setNextEvent,
					backEvent,
					setBackEvent,
					nextDisabled,
					setNextDisabled
				}}
			>
				<nav className="main-nav">
					<TopButton phase="D" />
					<TopButton phase="E" />
					<TopButton phase="B" />
					<TopButton phase="R" />
					<TopButton phase="IE" />
					<TopButton phase="F" />
				</nav>
				<div className="main-view">
					<Controller>
						<CardText 		phase="D" text />
						<CardText 		phase="E" list />
						<TwoDivOutline 	phase="B" />
						<SlideShow 		phase="R" />
						<FeedbackSlides phase="IE" log={log} scenario={scenario} />
						<CardText 		phase="F" list form />
					</Controller>
				</div>
			</AppContext.Provider>
		</div>
      );
};

export default MainOutline;