import { useApp } from "./AppContext";

/**
 * To disabled the next button, use ***app.setNextDisabled(true)*** and be sure to re-enable it when intended
 * @param {{}[]} children - array of phase components and their contents
 */
const Controller = ({ children }) => {
    const app = useApp();
    const displayedPhase = children.find(child => app.currPhase === child.props.phase);

    return (
    <>
        {displayedPhase}

        <div className="controller-nav">
            <button disabled={!app.prev_links[app.currPhase]} type="button" className="prev nav-btn" onClick={app.backEvent}>Back</button>
            <button disabled={!app.next_links[app.currPhase] || app.nextDisabled} type="button" className="next nav-btn" onClick={app.nextEvent}>Next</button>
        </div>
    </>
    );
}

export default Controller;