import { useState, useEffect } from "react";
import { useApp } from "./AppContext";
import { useInit } from "../utils";

import { SC8CP_PhaseIE as IE_Data } from "@meddbriefer/feedback-data/SC8CP_data_PhaseIE";
import { summarizeAnalysis } from '@meddbriefer/analysis'

import FeedbackPrompt from "./FeedbackPrompt";
import FeedbackAnalysis from "./FeedbackAnalysis";
import ExamineForm from "./ExamineForm";

export default function FormSlides({phase, log, scenario} ) {
    const app = useApp();
    const [actionNum, setActionNum]   = useState(0);
    const [userRating, setUserRating] = useState();

    const [actionQueue, setActionQueue] = useState(flattenActions([IE_Data.Action]));
    const currentAction = actionQueue[actionNum];

    const [examineQueue, setExamineQueue] = useState(currentAction?.subActions?.filter(a => a.examine));
    const [isExamining, setIsExamining]   = useState(false);

    useInit(() => {
        app.enablePhase(phase);
        app.setCurrPhase(phase);

        let newSummary = summarizeAnalysis(log.events, scenario, log.constraintsViolated)
        let summary = Object.values(newSummary).flat();

        // Merging the fields together makes things infinitely easier
        // Log fields are prefixed with "log_"
        let merged = mergeActionLog(actionQueue, summary);

        // Drop if misordered but not an error
        merged = merged.filter(action => {
            return !(action.type === "header"
                    && action.log_phaseOrderStatus !== "good"
                    && action.log_status !== "error");
            });

            // Filter out the calculated "good" actions from queue -- they don't need to be analyzed
            merged = merged.filter(action => action.log_status !== "good");

            // console.log(merged.find(e => e.id[0] === "assess-major-bleeding"));

            setActionQueue(merged);

    });
    console.log(currentAction)
    // This is where it is decided what to Examine
    useEffect(() => {
        const isTopLvl = a => a.subActions.length > 0;

        let newExamineQueue = currentAction.subActions
            .filter(action => !isTopLvl(action) && action.examine && ["absent", "errors", "error"].includes(action.log_status));

        if (isTopLvl(currentAction) && currentAction.examine && currentAction.log_status === "absent") {
            newExamineQueue.unshift(currentAction); // Maybe inefficient function call?
        }

        setExamineQueue(newExamineQueue);
    }, [currentAction]);

    useEffect(() => {
        if (actionNum >= actionQueue.length - 1) {
            // If last action to Investigate & Examine...
            app.setNextEvent(() => () => {
                app.setCurrPhase(app.next_links[phase] ?? phase);
            });
        } else {
            if (examineQueue.length > 0 && !isExamining) {
                app.setNextEvent(() => () => {
                    setIsExamining(true);
                });
                return;
            }

            if (examineQueue.length > 1) {
                app.setNextEvent(() => () => {
                    setExamineQueue(queue => queue.slice(1));
                });
            } else {
                app.setNextEvent(() => () => {
                    setUserRating(null);
                    setIsExamining(false);
                    setActionNum(a => a + 1);
                });
            }
        }

        app.setBackEvent(() => () => {
            if (actionNum === 0) {
                app.setCurrPhase(app.prev_links[phase] ?? phase)
            } else {
                setUserRating(null);
                setActionNum(num => num - 1);
            }
        });
        // eslint-disable-next-line
    }, [isExamining, examineQueue, actionQueue, currentAction]);

    return (
        <div className="feedback-slide">
            {isExamining
                && <p className="examine-action-hint"><b>{ currentAction.label }</b></p>}

            {
            !isExamining &&
            <FeedbackPrompt
                action={currentAction}
                ratingState={[userRating, setUserRating]}
            />
            }

            {
            userRating &&
            <FeedbackAnalysis
                userRating={userRating}
                action={currentAction}
                actionQueueState={[actionQueue, setActionQueue]}
                examineState={[examineQueue, isExamining]}
            />
            }

            {
            isExamining && examineQueue.length > 0 &&
            <ExamineForm examine={examineQueue[0]} />
            }
        </div>
    );
}

/************* Utility functions *************/

function flattenActions(action) {
    if (!action || action.subActions === []) return [];

    let flattened = [];

    for (const a of action)
        if (a.subActions.length > 0 && a.subActionsList)
            flattened.push(a, ...flattenActions(a.subActions));

    return flattened;
}

function mergeActionLog(actions, log) {
    for (let action of actions) {
        const match = log.find(e => action.id.some(a => a === e.id));

        if (!match) {
            console.warn(`No log match found for action: '${action.id[0]}'`);
            continue;
        }

        for (let field in match) {
            action[`log_${field}`] = match[field];
        }

        action.subActions = mergeActionLog(action.subActions, log);
    }

    return actions;
}
