import React, {useEffect, useState} from "react";
import { useApp } from "./AppContext";
// import Slide from "./Slide";

const TwoDivOutline = ({ phase }) => {
    const app = useApp(); 
    const [curSlide, setCurSlide] = useState(0); 
    const [enabledSteps, setEnabledSteps] = useState(0);
    const phaseInfo = app.stepData.find(step => step.step === app.currPhase);

    const active = (idx) => idx <= enabledSteps;

    useEffect(() => {
        app.enablePhase(phase);
        app.setCurrPhase(phase);

        if (curSlide > enabledSteps) setEnabledSteps(curSlide);

        app.setNextEvent(() => () => {
            if (curSlide >= phaseInfo.expectations.length - 1) {
                app.setCurrPhase(app.next_links[phase] ?? phase)
            } else {
                setCurSlide(slide => slide + 1);
            }
        });

        app.setBackEvent(() => () => {
            if (curSlide === 0) {
                app.setCurrPhase(app.prev_links[phase] ?? phase)
            } else {
                setCurSlide(slide => slide - 1);
            }
        });
    // eslint-disable-next-line
    }, [phase, curSlide]);

    return (
        <>
            <h2>Performance <b>B</b>enchmarks:</h2>
            <div className="two-div-outline-container">
                <div className="outline-box outline">
                    <p>Expected completed actions:</p>

                    <ol>
                        {phaseInfo.expectations.map((exp, idx) => (
                            <li 
                                key={exp.id} 
                                className={`outline-heading ${!active(idx) ? "disabled" : ""} ${idx === curSlide ? "active" : ""}`}
                                onClick={() => active(idx) && setCurSlide(idx)}
                            >
                                {exp.heading}
                            </li>
                        ))}
                    </ol>
                </div>

                <div className="outline-box outline-rhs">
                    <h4>{phaseInfo.expectations[curSlide].heading}</h4>

                    <ol>
                        {phaseInfo.expectations[curSlide].benchmarks.map(bm => (
                            <li key={bm.id}>
                                <p>{bm.text}</p>
                                <ol>
                                    {bm.subBenchmarks?.map((sBm, idx) => (
                                        sBm.phase?.[app.currPhase]?.text &&
                                        <li key={idx}>{sBm.phase[app.currPhase].text}</li>
                                    ))}
                                </ol>
                            </li>
                        ))}
                    </ol>
                </div>
            </div>
        </>
    );
}

export default TwoDivOutline;